import React, {Component} from 'react'
import PropTypes from 'prop-types'
import {Link} from 'react-router-dom'
import { StyleSheet, css } from 'aphrodite'
import Logo from './logo'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import {faFacebook, faTwitter, faInstagram} from '@fortawesome/fontawesome-free-brands'

const NavbarComponent = ({menuActive, onBurgerClick, onBurgerBlur}) => <nav className='navbar'>
  <div className='container' >
    <div className='navbar-brand'>
      <Link onClick={onBurgerBlur} className={`${css(styles.brand)} navbar-item`} to='/'>
        <Logo classNames={{
          logo: css(styles.logo)
        }} />
      </Link>
      <a className='navbar-item is-hidden-desktop' href='https://www.facebook.com/mgda.mfsd'>
        <span className='icon'>
          <FontAwesomeIcon icon={faFacebook} className={css(styles.icon)} />
        </span>
      </a>
      <a className='navbar-item is-hidden-desktop' href='https://www.instagram.com/MGDanceAcademy'>
        <span className='icon'>
          <FontAwesomeIcon icon={faInstagram} className={css(styles.icon)} />
        </span>
      </a>
      <a className='navbar-item is-hidden-desktop' href='https://twitter.com/MGDanceAcademy'>
        <span className='icon'>
          <FontAwesomeIcon icon={faTwitter} className={css(styles.icon)} />
        </span>
      </a>
      <button className={`${css(styles.burger)} button navbar-burger`} onClick={onBurgerClick}>
        <span />
        <span />
        <span />
      </button>
    </div>
    <div className={`navbar-menu is-${menuActive}`}>
      <div className='navbar-start'>
        <Link onClick={onBurgerBlur} to='/classes' className='navbar-item'>Classes</Link>
        <Link onClick={onBurgerBlur} to='/events' className='navbar-item'>Events</Link>
        <Link onClick={onBurgerBlur} to='/contact' className='navbar-item'>Contact</Link>
      </div>
      <div className='navbar-end is-hidden-touch'>
        <a className='navbar-item' href='https://www.facebook.com/mgda.mfsd'>
          <span className='icon'>
            <FontAwesomeIcon icon={faFacebook} className={css(styles.icon)} />
          </span>
        </a>
        <a className='navbar-item' href='https://www.instagram.com/MGDanceAcademy'>
          <span className='icon'>
            <FontAwesomeIcon icon={faInstagram} className={css(styles.icon)} />
          </span>
        </a>
        <a className='navbar-item' href='https://twitter.com/MGDanceAcademy'>
          <span className='icon'>
            <FontAwesomeIcon icon={faTwitter} className={css(styles.icon)} />
          </span>
        </a>
      </div>
    </div>
  </div>
</nav>

NavbarComponent.propTypes = {
  menuActive: PropTypes.string.isRequired,
  onBurgerClick: PropTypes.func.isRequired,
  onBurgerBlur: PropTypes.func.isRequired
}

const styles = StyleSheet.create({
  brand: {
    padding: '0',
    width: 100,
    height: 60
  },
  burger: {
    border: 'none',
    height: 60,
    borderRadius: 0
  },
  logo: {
    padding: '0 10px',
    height: 'auto',
    cursor: 'pointer'
  },
  icon: {
    width: '100%',
    height: 'auto'
  }
})

class Navbar extends Component {
  constructor (props) {
    super(props)
    this.state = {
      menuActive: 'inactive'
    }
  }
  componentDidMount () {
    window.onresize = () => {
      this.setState({
        menuActive: 'inactive'
      })
    }
  }
  onBurgerClick () {
    this.setState({
      menuActive: this.state.menuActive === 'active' ? 'inactive' : 'active'
    })
  }
  onBurgerBlur () {
    this.setState({
      menuActive: 'inactive'
    })
  }
  render () {
    return <NavbarComponent
      menuActive={this.state.menuActive}
      onBurgerClick={() => { this.onBurgerClick() }}
      onBurgerBlur={() => { this.onBurgerBlur() }}
    />
  }
}

export default Navbar



