import React from 'react'
import {StyleSheet, css} from 'aphrodite'

const Home = () => <section className='section'>
  <div className='container'>
    <figure className={`${css(styles.figure)} image`}>
      <img className={css(styles.image)} src='banner.jpg' alt='banner' />
    </figure>
    <div className={`${css(styles.message)} box`}>
      <div className='content'>
        <h3>Welcome to the MG Dance Academy!</h3>
        <p>We are a fun and friendly dance school based in Kendal and Keswick in the Lake District, teaching a range of different dance styles to children and young people of all abilities.</p>
        <p>Our national award winning principal, Miss Melanie, is a fully qualified teacher with the AAD and Acrobatic Arts, delivering high quality dance and acrobatic training to the individual, inspiring them to become the best they can be.</p>
        <p>We teach ballet, tap, modern, acrobatics, lyrical and contemporary offering exams, medal tests and performance opportunities as well as local and national competitions. We also regularly have the opportunity for students to take part in workshops and intensive programs with local experts and industry professionals to further their dance and acro training.</p>
        <p>Message us today to become one of our MGDA stars!</p>
      </div>
    </div>
  </div>
</section>

const styles = StyleSheet.create({
  figure: {
    height: 'auto',
    width: '80%',
    margin: 'auto',
    marginTop: -20,
    position: 'relative'
  },
  image: {
  },
  message: {
    marginTop: 20
  }
})

export default Home



