import React from 'react'
import {StyleSheet, css} from 'aphrodite'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import {faMapMarkerAlt, faLink} from '@fortawesome/fontawesome-free-solid'

const events = [{
  title: {
    label: 'The Lakes Players Panto'
  },
  link: {
    label: 'The Lakes Players',
    href: 'http://www.lakesplayers.co.uk/wpsite/'
  },
  location: {
    label: 'The Lakes School',
    href: 'https://goo.gl/maps/oyRmHWd6Sd32'
  },
  dates: [{
    label: '24th Jan - 28th Jan'
  }],
  expires: new Date('2018-01-28').getTime()
}, {
  title: {
    label: 'Medals and Exams'
  },
  location: {
    label: 'Ambleside',
    href: 'https://goo.gl/maps/hWjTSjgub1Q2'
  },
  dates: [{
    label: '24th March',
    tag: 'tbc'
  }],
  expires: new Date('2018-03-24').getTime()
}, {
  title: {
    label: 'Medals and Exams'
  },

  location: {
    label: 'Studio Nine, Workington',
    href: 'https://goo.gl/maps/DV9nzxK23KB2'
  },
  dates: [{
    label: '25th March',
    tag: 'tbc'
  }],
  expires: new Date('2018-03-25').getTime()
}, {
  title: {
    label: 'Matthew Bournes Cinderella',
    tag: 'full'
  },
  link: {
    label: 'The Show',
    href: 'https://www.thelowry.com/events/matthew-bournes-cinderella'
  },
  location: {
    label: 'The Lowry, Salford',
    href: 'https://www.thelowry.com/plan-your-visit/find-us'
  },
  dates: [{
    label: '17th March'
  }],
  expires: new Date('2018-03-17').getTime()
}, {
  title: {
    label: 'The AAD Gala',
    tag: 'Blackpool troupes only'
  },
  location: {
    label: 'Winter Gardens, Blackpool',
    href: 'https://goo.gl/maps/M8mm1Xt54As'
  },
  dates: [{
    label: '15th April'
  }],
  expires: new Date('2018-04-15').getTime()
}, {
  title: {
    label: 'The Annual Show'
  },
  location: {
    label: 'Sands Centre, Carlisle',
    href: 'https://goo.gl/maps/MUaeDPmMFau'
  },
  dates: [{
    label: '10th June'
  }],
  expires: new Date('2018-06-10').getTime()
}, {
  title: {
    label: 'The AAD Scholarship Competition'
  },
  location: {
    label: 'Buxton',
    href: 'https://goo.gl/maps/A9ZKpcQkMzw'
  },
  dates: [{
    label: '17th June'
  }],
  expires: new Date('2018-06-17').getTime()
}, {
  title: {
    label: 'Medals and Exams'
  },
  location: {
    label: 'Studio Nine, Workington',
    href: 'https://goo.gl/maps/DV9nzxK23KB2'
  },
  dates: [{
    label: '21st July',
    tag: 'tbc'
  }],
  expires: new Date('2018-07-21').getTime()
}, {
  title: {
    label: '(MFSD) Summer School'
  },
  location: {
    label: 'Studio Nine, Workington',
    href: 'https://goo.gl/maps/DV9nzxK23KB2'
  },
  dates: [{
    label: '23rd - 27th July'
  }],
  expires: new Date('2018-07-27').getTime()
}, {
  title: {
    label: 'Summer Acro Intensive'
  },
  location: {
    label: 'Keswick',
    href: 'https://goo.gl/maps/YsnSy4e6nYw'
  },
  dates: [{
    label: '28th - 30th August'
  }],
  expires: new Date('2018-08-30').getTime()
}]

const Events = () => <section className='section'>
  {events
    .filter(event => event.expires > new Date().getTime())
    .map((event, index) => <div key={`event-${index}`} className='box'>
      <div className='content'>
        <h1 className={css(styles.title)}>
          {event.title.hasOwnProperty('tag') ? <span className={`${css(styles.tag)} tag is-warning is-medium`}>{event.title.tag}</span> : null}
          <span> {event.title.label}</span>
        </h1>
        {event.link ? <p className={css(styles.link)}>
          <a>
            <span className='icon'>
              <FontAwesomeIcon icon={faLink} />
            </span>
            <span> {event.link.label}</span>
          </a>
        </p> : null}
        {event.location ? <h4>
          {event.location.hasOwnProperty('tag') ? <span className={`${css(styles.tag)} tag is-warning`}>{event.location.tag}</span> : null}
          <a href={event.location.href}>
            <span className='icon'>
              <FontAwesomeIcon icon={faMapMarkerAlt} />
            </span>
            <span> {event.location.label}</span>
          </a>
        </h4> : null
        }
        {event.dates.map((date, dateIndex) => <p key={`event-${index}-date-${dateIndex}`} className={css(styles.date)}>
          {date.hasOwnProperty('tag') ? <span className={`${css(styles.tag)} tag is-warning`}>{date.tag}</span> : null}
          <i> {date.label}</i>
        </p>)}
      </div>
    </div>)}
</section>

const styles = StyleSheet.create({
  tag: {
    verticalAlign: 'middle'
  },
  title: {
    marginTop: 0,
    marginBottom: 5
  },
  link: {
    fontSize: '1.25em',
    marginTop: 0,
    marginBottom: 5
  },
  icon: {

  },
  date: {
    marginBottom: 5
  }
})

export default Events



