import React from 'react'
import { StyleSheet, css } from 'aphrodite'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import {faMapMarkerAlt} from '@fortawesome/fontawesome-free-solid'

const classes = [{
  day: 'Monday',
  img: 'monday.jpg',
  labels: ['Kendal'],
  location: 'Hallgarth Community Centre',
  href: 'https://goo.gl/maps/KqHiuTUUDS52',
  lessons: [{
    time: '16:00 - 17:00',
    label: 'Babes'
  }, {
    time: '17:00 - 18:00',
    label: 'Juniors'
  }, {
    time: '18:00 - 19:00',
    label: 'Acro'
  }, {
    time: '19:00 - 20:00',
    label: 'Inters'
  }]
}, {
  day: 'Tuesday',
  img: 'tuesday.jpg',
  label: 'Keswick Acro',
  location: 'Keswick Methodist Church',
  href: 'https://goo.gl/maps/YsnSy4e6nYw',
  lessons: [{
    time: '16:00 - 17:00',
    label: 'Lilac Class'
  }, {
    time: '17:00 - 18:00',
    label: 'Purple Class'
  }, {
    time: '18:00 - 19:00',
    label: 'Indigo Class'
  }, {
    time: '19:00 - 20:00',
    label: 'Violet Class'
  }]
}, {
  day: 'Wednesday',
  img: 'wednesday.jpg',
  labels: ['Keswick Privates &', 'Competition Team'],
  location: 'Keswick Methodist Church',
  href: 'https://goo.gl/maps/YsnSy4e6nYw',
  lessons: [{
    time: '15.45 - 18:00',
    label: 'Private Lessons'
  }, {
    time: '18:00 - 19:00',
    label: 'Competition team rehearsal'
  }, {
    time: '19:00 - 19:30',
    label: 'Private Lessons'
  }]
}, {
  day: 'Thursday',
  img: 'thursday.png',
  labels: ['Ambleside Privates'],
  location: 'The Kelsick Centre',
  href: 'https://goo.gl/maps/QTTQQSgJYAs',
  lessons: [{
    time: '16:00 - 17:45',
    label: 'Private Lessons'
  }]
}, {
  day: 'Friday',
  img: 'friday.png',
  labels: ['Keswick Dance', 'MFSD'],
  location: 'Keswick Methodist Church',
  href: 'https://goo.gl/maps/YsnSy4e6nYw',
  lessons: [{
    time: '16:00 - 17:00',
    label: 'Babes'
  }, {
    time: '17:00 - 18:00',
    label: 'Juniors'
  }, {
    time: '18:00 - 19:00',
    label: 'Inters'
  }, {
    time: '19:00 - 19:30',
    label: 'Private Lessons'
  }]
}]

const Classes = () => <section className='section'>
  <div className='container'>
    {classes.map(value => <div key={value.day} className='box'>
      <div className='columns'>
        <div className='column is-4'>
          <div key={value.day} className='content'>
            <h1>{value.day}</h1>
            <h4>
              {value.labels ? value.labels.map((label, index) => <span className={css(styles.label)} key={`${value.day}-label-${index}`}>{label}</span>) : <span className={css(styles.label)}>{value.label}</span>}
              <a href={value.href}>
                <span className='icon'>
                  <FontAwesomeIcon icon={faMapMarkerAlt} />
                </span>
                <span>{value.location}</span>
              </a>
            </h4>
          </div>
        </div>
        <div className='column'>
          <table className='table is-bordered is-fullwidth'>
            <thead>
              <tr>
                <th className={css(styles.time)}>Time</th>
                <th className={css(styles.class)}>Class</th>
              </tr>
            </thead>
            <tbody>
              {value.lessons.map(lesson => <tr key={`${value.day}-${lesson.time}`}>
                <td className={css(styles.time)}>{lesson.time}</td>
                <td>{lesson.label}</td>
              </tr>)}
            </tbody>
          </table>
        </div>
        <div className='column is-hidden-touch'>
          <img src={value.img} alt={value.day} />
        </div>
      </div>
    </div>)}
  </div>
</section>

const styles = StyleSheet.create({
  label: {
    display: 'block',
    marginBottom: 10
  },
  time: {
    width: 130,
    textAlign: 'center'
  },
  class: {
  }
})

export default Classes



