import React from 'react'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import {faPhone, faEnvelope} from '@fortawesome/fontawesome-free-solid'
import {faFacebook, faTwitter, faInstagram} from '@fortawesome/fontawesome-free-brands'
import { StyleSheet, css } from 'aphrodite'

const Contact = () => <section className='section'>
  <div className='container'>
    <div className='columns'>
      <div className='column'>
        <div className='box'>
          <h1 className='subtitle'>Send a Message</h1>
          <form action='//formspree.io/melaniegpreston@gmail.com' method='POST'>
            <div className='field'>
              <label className='label'>First Name</label>
              <div className='control'>
                <input className='input' type='text' name='fname' placeholder='First Name' required />
              </div>
            </div>
            <div className='field'>
              <label className='label'>Last Name</label>
              <div className='control'>
                <input className='input' type='text' name='lname' placeholder='Last Name' required />
              </div>
            </div>
            <div className='field'>
              <label className='label'>Email Address</label>
              <div className='control'>
                <input className='input' type='email' name='_replyto' placeholder='me@email.com' required />
              </div>
            </div>
            <div className='field'>
              <label className='label'>Subject</label>
              <div className='control'>
                <input className='input' type='text' name='_subject' placeholder='Subject' required />
              </div>
            </div>
            <div className='field'>
              <label className='label'>Message</label>
              <div className='control'>
                <textarea className='textarea' name='message' placeholder='Message' required />
              </div>
            </div>
            <input type='hidden' name='_next' value='/thanks' />
            <input type='text' name='_gotcha' style={{display: 'none'}} />
            <div className='field is-grouped'>
              <div className='control'>
                <input className='button is-link' type='submit' value='Send' />
              </div>
              <div className='control'>
                <input className='button is-text' type='reset' value='Reset' />
              </div>
            </div>
          </form>
        </div>
      </div>
      <div className='column'>
        <div className='box'>
          <h1 className='subtitle'>Contacts and Social Media</h1>
          <div className={css(styles.contact)}>
            <a href='email:melaniegpreston@gmail.com'>
              <span className={`${css(styles.icon)} icon`}>
                <FontAwesomeIcon icon={faEnvelope} />
              </span>
              <span> melaniegpreston@gmail.com</span>
            </a>
          </div>
          <div className={css(styles.contact)}>
            <a href='phone:07581483606'>
              <span className={`${css(styles.icon)} icon`}>
                <FontAwesomeIcon icon={faPhone} />
              </span>
              <span> 07581483606</span>
            </a>
          </div>
          <div className={css(styles.contact)}>
            <a href='https://www.facebook.com/mgda.mfsd'>
              <span className={`${css(styles.icon)} icon`}>
                <FontAwesomeIcon icon={faFacebook} />
              </span>
              <span> Facebook</span>
            </a>
          </div>
          <div className={css(styles.contact)}>
            <a href='https://www.instagram.com/MGDanceAcademy'>
              <span className={`${css(styles.icon)} icon`}>
                <FontAwesomeIcon icon={faInstagram} />
              </span>
              <span> Instagram</span>
            </a>
          </div>
          <div className={css(styles.contact)}>
            <a href='https://twitter.com/MGDanceAcademy'>
              <span className={`${css(styles.icon)} icon`}>
                <FontAwesomeIcon icon={faTwitter} />
              </span>
              <span> Twitter</span>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>

const styles = StyleSheet.create({
  contact: {
    margin: '10px 0',
    fontSize: 20
  },
  icon: {
    verticalAlign: 'middle'
  }
})

export default Contact



