import React from 'react'
import {Switch, Route} from 'react-router'
import { StyleSheet, css } from 'aphrodite'

const viafont = {
  fontFamily: 'Viafont',
  fontStyle: 'normal',
  fontWeight: 'normal',
  src: 'url(/viafont/Viafont.otf)'
}

const Hero = () => <section className={`${css(styles.hero)} hero is-small`}>
  <div className='hero-body'>
    <div className='container'>
      <h1 className={`${css(styles.title)} title`}>
        <Switch>
          <Route exact path='/'>
            <span>MGDA</span>
          </Route>
          <Route path='/classes'>
            <span>Classes</span>
          </Route>
          <Route path='/events'>
            <span>Events</span>
          </Route>
          <Route path='/contact'>
            <span>Contact</span>
          </Route>
          <Route path='/thanks'>
            <span>Thank You</span>
          </Route>
          <Route path='/*'>
            <span>Missing</span>
          </Route>
        </Switch>
      </h1>
    </div>
  </div>
</section>

const styles = StyleSheet.create({
  hero: {
    backgroundColor: '#82298A'
  },
  title: {
    color: '#fff',
    fontFamily: [viafont],
    fontWeight: 'normal'
  }
})

export default Hero



