import React from 'react'
import { StyleSheet, css } from 'aphrodite'

const Thanks = () => <section className='section'>
  <div className='container'>
    <div className={`${css(styles.notification)} notification`}>
      <div className='content'>
        <p className={css(styles.thankYou)} >Thank you for contacting MGDA</p>
        <p className={css(styles.weWillRespond)}>We have received your message and will respond as soon as possible</p>
      </div>
    </div>
  </div>
</section>

const styles = StyleSheet.create({
  notification: {
    backgroundColor: '#82298A',
    color: '#fff'
  },
  thankYou: {
    fontSize: 24,
    textAlign: 'center'
  },
  weWillRespond: {
    fontSize: 18,
    textAlign: 'center'
  }
})

export default Thanks



